@import "common/mixins";
@import "common/variables";

.training {
  &__body {
    position: relative;
    @include jucb();
    @media screen and (max-width: 920px) {
      @include jucc();
      padding: 5% 6% 15% 6%;
    }

    width: 100%;
    height: 100%;
    padding: 14% 0% 0% 6%;
    &_breack {
      padding: 14% 8% 0% 18%;
      @media screen and (max-width: 920px) {
        @include jucc();
        padding: 5% 6% 15% 6%;
      }
    }
  }
  &__side {
    position: absolute;
    z-index: 10;
    width: 15%;
    height: 5%;
    top: 21%;
    right: -10%;
    @media screen and (max-width: 1350px) {
      right: 47%;
    }
    @media screen and (max-width: 960px) {
      right: -2000%;
    }
    &_breack {
      top: 20%;
      left: 9%;
      @media screen and (max-width: 1350px) {
        left: 18%;
      }
      @media screen and (max-width: 960px) {
        left: -2000%;
      }
    }
  }

  // ----ширина единичного блока-----
  &__item {
    flex: 0 1 24%;
    &_breack {
      flex: 0 1 38%;
    }
    @media screen and (max-width: 926px) {
      flex: 0 1 100%;
    }
  }

  &__serfer {
    flex: 0 1 66%;
    padding: 38% 0px 0px 0px;
    @media screen and (max-width: 926px) {
      flex: 1 1 100%;
      padding: 50% 10% 0% 10%;
    }
    &_breack {
      flex: 0 1 54%;
      padding: 48% 0px 0px 0px;
      @media screen and (max-width: 926px) {
        flex: 1 1 100%;
        padding: 80% 10% 0% 10%;
      }
    }
  }
}
.serfer {
  position: relative;
}
.side {
  @include Text-Style-7;
  line-height: 2;
  text-transform: uppercase;
  @include rotate(-90deg);
  @media screen and (max-width: 1350px) {
    @include rotate(0deg);
  }
}
// =====единичный блок=========

.tiny-training {
  width: 100%;
  height: 100%;
  &__body {
    position: relative;
    text-align: left;
    @include flex();
    padding: 30% 0% 10% 0%;
    @media screen and (max-width: 960px) {
      padding: 20% 0% 10% 0%;
    }
    @media screen and (max-width: 460px) {
      padding: 40% 0% 10% 0%;
    }
    position: relative;
  }

  &__title {
    position: relative;
    z-index: 2;
    flex: 0 1 100%;
    @include Text-Style-18;
    line-height: 1;
    &:before {
      content: "";
      background-color: #5da8ef;
      position: absolute;
      width: 22px;
      height: 2px;
      top: 112%;
      left: 0;
      z-index: 2;
    }
    &:after {
      content: attr(data-number);
      position: absolute;
      @include Text-Style-21;
      text-transform: uppercase;
      z-index: -2;
      position: absolute;
      width: 50%;
      height: 100%;
      top: -33%;
      left: 0%;
      transform: translate(0%, -50%);
    }
  }

  &__text {
    &_one {
      flex: 0 1 100%;
      @include Text-Style-7;
      line-height: 1;
      text-transform: uppercase;
      padding: 20% 0% 0% 0%;
      @media screen and (max-width: 926px) {
        padding: 5% 0% 0% 0%;
      }
      @media screen and (max-width: 400px) {
        padding: 10% 0% 0% 0%;
      }
    }

    &__button {
      position: relative;
      z-index: 2;
      flex: 0 1 100%;

      a {
        @include jucc();
      }
      span {
        @include Text-Style-9;
        line-height: 1;
        text-transform: uppercase;
      }
    }
  }
}

.button-item-plaza-discription {
  &_training {
    margin: 8% 0px 0px 0px;
  }
}
